.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  justify-content: space-around;
  text-align: center;
  overflow-x: auto;
}

td,
th {
  padding: 5px;
}

table {
  padding: 10px;
  overflow-x: auto;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0px;
}

tr:nth-child(even) {
  background-color: rgb(124, 128, 97);
}